.dashboard {
  width: 80%;
  margin: 40px auto;
  font-family: Arial, Helvetica, sans-serif;
}

.title {
  text-align: center;
  margin-bottom: 20px;
  color: #fff;
}

.empty {
  text-align: center;
  color: gray;
}

.userCard {
  background: white;
  padding: 15px 20px;
  margin-bottom: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.userCard span {
  font-weight: bold;
}
