*{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background: linear-gradient(135deg, #667eea, #764ba2);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form {
  background: white;
  padding: 30px;
  border-radius: 10px;
  width: 350px;
}

.btn {
  display: flex;
  margin-bottom: 20px;
}

.btn button {
  flex: 1;
  padding: 10px;
  border: none;
  cursor: pointer;
  background: #eee;
  font-weight: bold;
  transition: 0.3s;
}

.btn button:hover {
  background: #ddd;
}

h2 {
  text-align: center;
  margin-bottom: 15px;
}

table {
  width: 100%;
}

td {
  padding: 8px 0;
}

input {
  width: 100%;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #ccc;
  outline: none;
  transition: 0.3s;
}

input:focus {
  border-color: #667eea;
  box-shadow: 0 0 5px rgba(102, 126, 234, 0.5);
}

button[type="submit"] {
  width: 100%;
  padding: 10px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

button[type="submit"]:hover {
  background: #5a67d8;
}
